@font-face {
  font-family: "Daughter_handwriting";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/naverfont_03@1.0/Daughter_handwriting.woff")
    format("woff");
}

header {
  font-size: 30px;
  font-family: "Daughter_handwriting";
  margin: 10px 20px 0;
  color: rgb(90, 90, 90);
}

.questionBox {
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

button {
  width: auto;
  border-style: none;
  background-color: rgba(223, 219, 203, 0.753);
  padding: 5px 15px;
  border-radius: 2px;
}

.interviewButton {
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.question {
  margin-top: 20px;
  text-align: center;
  font-family: "Daughter_handwriting";
  font-size: 30px;
  width: 100%;
}

.question h1 {
  font-weight: 100;
  color: rgb(90, 90, 90);
  margin-bottom: 50px;
}

.questionInput {
  text-align: center;
  width: 70vw;
  height: 50vh;
  background-color: rgba(224, 212, 197, 0.274);
  border-style: none;
  font-family: "Daughter_handwriting";
  font-size: 20px;
  padding: 15px;
}

.questionInput:focus {
  outline: 0;
}

.question button {
  display: block;
  margin: 30px auto 0;
}

.question button:hover {
  background-color: rgb(224, 212, 197);
}
